import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, Subject, asapScheduler, pipe, of, from, interval, merge, fromEvent } from 'rxjs';
import * as jwt_decode from "jwt-decode";
import { VehicleService, PostVehicleService } from '../Models/VehicleService';
import { SRProviders } from '../Models/SRProvider';

@Injectable({
    providedIn: 'root'
})

export class vsService{
    baseURL= "https://localhost:44349/api/";
    //baseURL = "http://35.178.156.37/api/";

    constructor(private http: HttpClient) { }

    getVehicleService():any{
        const ops = {   
            headers: new HttpHeaders({
              'Authorization': 'Bearer ' + localStorage.getItem("jwtToken")
            })
          };
        return this.http.get<VehicleService>(this.baseURL + "VehicleService/" + localStorage.getItem("CurrentVehicle"),ops);
    }

    getServiceProviders(){
        const ops = {
            headers: new HttpHeaders({
              'Authorization': 'Bearer ' + localStorage.getItem("jwtToken")
            })
          };
        return this.http.get<SRProviders>(this.baseURL + "VehicleService/ServiceProviders/" + localStorage.getItem("ServiceProvider"),ops)
    }

    getVehicleServiceDetails(id:number):any{
        const ops = { 
            headers: new HttpHeaders({
              'Authorization': 'Bearer ' + localStorage.getItem("jwtToken")
            })
          };
        return this.http.get<VehicleService>(this.baseURL+ "VehicleService/" + id.toString(),ops);
    }

    putVehicleService(VehicleService: PostVehicleService, id:number){
        const ops = { 
            headers: new HttpHeaders({
              'Authorization': 'Bearer ' + localStorage.getItem("jwtToken")
            })
          };
          return this.http.put(this.baseURL + "VehicleService/" + id.toString(),VehicleService,ops);
    }

    postVehicleService(VehicleService: PostVehicleService):any{
        const ops = { 
            headers: new HttpHeaders({
              'Authorization': 'Bearer ' + localStorage.getItem("jwtToken")
            })
          };
       return this.http.post(this.baseURL + "VehicleService/",VehicleService,ops);
    }

    deleteVehicleService(id:number){
        const ops = { 
            headers: new HttpHeaders({
              
              'Authorization': 'Bearer ' + localStorage.getItem("jwtToken")
            })
          };
          return this.http.delete(this.baseURL + "VehicleService" + id.toString(),ops);
    }

    putSendService(){

    }

    postSendService(){

    }

    getServiceNotification(){

    }

   

}