import {  OnInit } from '@angular/core';
import { Settings } from 'src/app/app.settings.model';
import { AppSettings } from 'src/app/app.settings';
import { MatTableDataSource } from '@angular/material/table';
import { Component, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { SRProviders } from 'src/app/Models/SRProvider';

export interface Element {
  Provider_ID: string;
  Provider_Name: string;
  Provider_Address: string;
  Provider_Contact: string;
  Provider_Email: string;
  }

@Component({
  selector: 'app-srProvider',
  templateUrl: './sr-provider.component.html',
  styleUrls: ['./sr-provider.component.scss']
})
export class SrProviderComponent implements OnInit {

  public displayedColumns = ['Provider_ID', 'Provider_Name','Provider_Contact','Provider_Email','Provider_Address','Actions'];
  public dataSource: any;
  public settings: Settings;
  show: boolean = false;
  hideTable = true;
  showAdd: boolean = false;
  @ViewChild(MatPaginator) paginator: MatPaginator;

 data: Element[] = [
    {Provider_ID: "1",Provider_Name: "Jon Auto", Provider_Address: "12 Long Street Vaalpark", Provider_Contact: "0169761235", Provider_Email: "jauto@gmail.com"},
    {Provider_ID: "2",Provider_Name: "Mercedes", Provider_Address: "13 Gutem Road Vereeniging", Provider_Contact: "0569667825", Provider_Email: "support@mercedes.com"},
    {Provider_ID: "3",Provider_Name: "Piet Spares", Provider_Address: "8 Short Lane Sandton", Provider_Contact: "0139763255", Provider_Email: "piet@spares.com"},
  ];
  
 types: string[] = [
   "Wiring","Structural","Security", "Miscellaneous"
 ]

SRProviderEdit: SRProviders;
SRProviderAdd: SRProviders;
AdminPriv: string;
selectedValue: string;
SRProviderSelect: string;

  constructor(public appSettings:AppSettings) {
    this.settings = this.appSettings.settings; 
    this.dataSource = new MatTableDataSource<Element>(this.data);
  }
  ngOnInit(): void {
     this.AdminPriv = localStorage.getItem("PrivelageLevel");
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
  }

  changeform(){
    if(this.show == false){
      this.show =true;
      this.showAdd = false;
      this.hideTable = false;
    }
  }

  getSRProviderDetail(Id: number){
    //Implement function to pupulate edit field and populate vehicle edit\
    this.SRProviderEdit = new SRProviders;
    this.SRProviderEdit.Provider_Name = "New Name";
    this.SRProviderEdit.Provider_Name = "New Number";
    this.SRProviderEdit.Provider_Name = "New Email";
    this.SRProviderEdit.Provider_Name = "New Address";
    this.changeform();
  }

  AddSRProvider(){
    if(this.show == true){
         this.show = false;
    }
    this.SRProviderAdd = new SRProviders;
    this.showAdd = true;
    this.hideTable = false;
  }

  PutSRProvider(){
    alert("Done");
  }

  deleteSRProvider(){
    alert("Are you sure?");
  }
 
  back(){
    this.show = false;
    this.showAdd = false;
    this.hideTable = true;
  }
}
