import {  OnInit } from '@angular/core';
import { Settings } from '../app.settings.model';
import { AppSettings } from '../app.settings';
import { MatTableDataSource } from '@angular/material/table';
import { Component, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { Infrastructures } from '../Models/Infrastructures'
import { ActivatedRoute } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';

export interface Element {
Infrastructure_ID: string;
Infrastructure_Type_Description: string;
Infrastructure_Name: string;
Infrastructure_Location: string;
Section_Name: string;
Infrastructure_Size: string;
is_Active: boolean;
}



@Component({
  selector: 'app-infrastructure',
  templateUrl: './infrastructure.component.html',
  styleUrls: ['./infrastructure.component.scss']
})
export class InfrastructureComponent implements OnInit {
  public displayedColumns = ['Infrastructure_ID', 'Infrastructure_Name', 'Infrastructure_Location','Infrastructure_Size', 'Section_Name', 'Infrastructure_Type_Description', 'is_Active','Actions'];
  public dataSource: any;
  public settings: Settings;
  id;
  infrastructure = {
    Infrastructure_Name: '',
    Infrastructure_Location: '',
    Infrastructure_Size: '',
    Section_Name: '',
    Infrastructure_Type_Description: '',
    is_Active: ''
  }
  show: boolean = false;
  showAdd: boolean = false;
  hideTable: boolean = true;
  @ViewChild(MatPaginator) paginator: MatPaginator;

 data: Element[] = [
    {Infrastructure_ID: "1",Infrastructure_Name: "South Coop", Infrastructure_Location: 'Next to Brown Barn',Infrastructure_Size: "30" ,Section_Name: "Veld 2", Infrastructure_Type_Description: 'Coop', is_Active: true},
    {Infrastructure_ID: "2",Infrastructure_Name: "Brown Barn", Infrastructure_Location: 'Next to Red Barn',Infrastructure_Size: "100" ,Section_Name: "Veld 1", Infrastructure_Type_Description: 'Barn', is_Active: true},
    {Infrastructure_ID: "3",Infrastructure_Name: "North Coop", Infrastructure_Location: 'Next to Red Barn',Infrastructure_Size: "30" ,Section_Name: "Veld 2", Infrastructure_Type_Description: 'Coop', is_Active: true},
    {Infrastructure_ID: "4",Infrastructure_Name: "Main House", Infrastructure_Location: 'Next to Shed',Infrastructure_Size: "120" ,Section_Name: "Veld 1", Infrastructure_Type_Description: 'House', is_Active: true},   
    {Infrastructure_ID: "5",Infrastructure_Name: "Staff House", Infrastructure_Location: 'Next to Shed',Infrastructure_Size: "300" ,Section_Name: "Veld 1", Infrastructure_Type_Description: 'House', is_Active: true},
    {Infrastructure_ID: "6",Infrastructure_Name: "Red Barn", Infrastructure_Location: 'Next to Red Barn',Infrastructure_Size: "100" ,Section_Name: "Veld 2", Infrastructure_Type_Description: 'Barn', is_Active: true},   
    {Infrastructure_ID: "7",Infrastructure_Name: "Shed", Infrastructure_Location: 'Next to Main House',Infrastructure_Size: "10" ,Section_Name: "Veld 2", Infrastructure_Type_Description: 'Barn', is_Active: true}

  ];
  
 types: string[] = [
   "Barn","House","Coop"
 ]

 sections: string[] = [
   "Veld 1", "Veld 2"
 ]

InfrastructureEdit: Infrastructures;
InfrastructureAdd: Infrastructures;
AdminPriv: string;
selectedValue: string;
SectionSelect: string;

constructor(public appSettings:AppSettings, private route:ActivatedRoute, private dialog: MatDialog) {
  this.settings = this.appSettings.settings; 
  this.dataSource = new MatTableDataSource<Element>(this.data);
}
  ngOnInit(): void {
     this.AdminPriv = localStorage.getItem("PrivelageLevel");
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
  }

  changeform(){
    if(this.show == false){
      this.show =true;
      this.showAdd = false;
      this.hideTable = false;
    }
  }

  getInfrastructureDetail(Id: number){
    //Implement function to pupulate edit field and populate vehicle edit\
    this.InfrastructureEdit = new Infrastructures;
    this.InfrastructureEdit.Active = true;
    this.InfrastructureEdit.Infrastructure_Type = "Coop";
    this.InfrastructureEdit.Infrastructure_Name = "South Coop";
    this.InfrastructureEdit.Infrastructure_Location = "Next to Red Barn";
    this.InfrastructureEdit.Infrastructure_Size= "30";
    this.InfrastructureEdit.Section= "Veld 2";
    this.InfrastructureEdit.Active = true;
  
    this.selectedValue = this.InfrastructureEdit.Infrastructure_Type;
    this.SectionSelect = this.InfrastructureEdit.Section;


    this.changeform();
  }

  GenerateReport(){
    alert("Report generated successfully!")
  }

  AddInfrastructure(){
    if(this.show == true){
         this.show = false;
    }
    this.InfrastructureAdd = new Infrastructures;
    this.showAdd = true;
    this.hideTable = false;
  }

  PutInfrastructure(){
    alert("Done");
  }

  deleteInfrastructure(){
    alert("Are you sure");
  }

  back(){
    this.show = false;
    this.hideTable = true;
    this.showAdd = false;
  }

  NewTestFunction() {
    
  }

}
