import {  OnInit } from '@angular/core';
import { Settings } from 'src/app/app.settings.model';
import { AppSettings } from 'src/app/app.settings';
import { MatTableDataSource } from '@angular/material/table';
import { Component, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { Faults } from 'src/app/Models/Fault';

export interface Element {
    Fault_ID: string;
    Fault_Description: string;
    Fault_Start_Date: string;
    Fault_End_Date: string;
    Fault_Type_Description: string;
    Status_Description: string;
    Importance_Description: string;
  }

@Component({
  selector: 'app-Fault-log',
  templateUrl: './fault-log.component.html',
  styleUrls: ['./fault-log.component.scss']
})
export class FaultLogComponent implements OnInit {

  public displayedColumns = ['Fault_ID', 'Fault_Type_Description', 'Fault_Description', 'Fault_Start_Date','Fault_End_Date','Status_Description','Importance_Description','Actions'];
  public dataSource: any;
  public settings: Settings;
  show: boolean = false;
  showAdd: boolean = false;
  @ViewChild(MatPaginator) paginator: MatPaginator;

 data: Element[] = [
    {Fault_ID: "1",Fault_Type_Description: "Infrastructure", Fault_Description: 'Check Barn wiring', Fault_Start_Date: "05/07/2020" , Fault_End_Date: "05/07/2020", Status_Description: 'In Progress', Importance_Description: 'Medium'},
    {Fault_ID: "2",Fault_Type_Description: "Equipment", Fault_Description: 'Clean Ploughs', Fault_Start_Date: "02/01/2020" , Fault_End_Date: "05/01/2020", Status_Description: 'Completed', Importance_Description: 'High'},
    {Fault_ID: "3",Fault_Type_Description: "Other", Fault_Description: 'Sweep Barn', Fault_Start_Date: "22/05/2020" , Fault_End_Date: "22/05/2020", Status_Description: 'Incomplete', Importance_Description: 'Low'},
  ];
  
 types: string[] = [
  "Wiring","Structural","Security"
 ]

 infrastructures: string[] = [
   "Building1","Building2"
 ]

 staff: string[] = [
   "John", "Petrus"
 ]

 equipment: string[] =[
   "Trailer", "Plow"
 ]

 status: string[] =[
  "In Progress", "Completed", "Pending"
]

importance: string[] =[
  "High", "Low", "Medium"
]

FaultEdit: Faults;
FaultAdd: Faults;
AdminPriv: string;
selectedValue: string;
EquipmentSelect: string;
StatusSelect: String;
ImportanceSelect: string;
InfrastructureSelect: string;
StaffSelect: string;
  constructor(public appSettings:AppSettings) {
    this.settings = this.appSettings.settings; 
    this.dataSource = new MatTableDataSource<Element>(this.data);
  }
  ngOnInit(): void {
     this.AdminPriv = localStorage.getItem("PrivelageLevel");
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
  }

  changeform(){
    if(this.show == false){
      this.show =true;
      this.showAdd = false;
    }
  }

  getFaultDetail(Id: number){
    //Implement function to pupulate edit field and populate vehicle edit\
    this.FaultEdit = new Faults;
    this.FaultEdit.Fault_Description = "Clean Ploughs";
    this.FaultEdit.Fault_Type_Description = "Equipment";
    this.FaultEdit.Infrastructure_Description= "Barn";
    this.FaultEdit.Equipment_Description= "Plough";
    this.FaultEdit.Status_Description= "Active";
    this.FaultEdit.Importance_Description= "High";
    this.FaultEdit.Staff_Name= "John";
    this.FaultEdit.Fault_Start_Date= "22/07/2020";
    this.FaultEdit.Fault_End_Date= "25/07/2020";
  
    this.selectedValue = this.FaultEdit.Fault_Type_Description;
    this.EquipmentSelect = this.FaultEdit.Equipment_Description;
    this.InfrastructureSelect = this.FaultEdit.Infrastructure_Description;
    this.StatusSelect = this.FaultEdit.Status_Description;
    this.ImportanceSelect = this.FaultEdit.Importance_Description;
    this.StaffSelect = this.FaultEdit.Staff_Name;


    this.changeform();
  }

  AddFault(){
    if(this.show == true){
         this.show = false;
    }
    this.FaultAdd = new Faults;
    this.showAdd = true;
  }

  PutFault(){
    alert("Done");
  }

  deleteFault(){
    alert("Are you sure");
  }

}
