import {  OnInit } from '@angular/core';
import { Settings } from '../app.settings.model';
import { AppSettings } from '../app.settings';
import { MatTableDataSource } from '@angular/material/table';
import { Component, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { Vehicles } from '../Models/Vehicles'
import { Router } from '@angular/router';
export interface Element {
  Type_ID: string;
  Type: string;

  }

  @Component({
    selector: 'app-vehicle-types',
    templateUrl: './vehicle-types.component.html',
    styleUrls: ['./vehicle-types.component.scss']
  })

export class VehicleTypesComponent implements OnInit {
  public displayedColumns = ['Type_ID', 'Type','Actions'];
  public dataSource: any;
  public settings: Settings;
  TypeEdit = "Truck";
  show: boolean = false;  //<<< show edit form
  showtable: boolean = true; //<<< show table
  showAdd: boolean = false; //<<< show add form
  @ViewChild(MatPaginator) paginator: MatPaginator;

  data: Element[] = [
    {Type_ID: "1",Type:"Car"},
    {Type_ID: "1",Type:"Truck"},
    {Type_ID: "1",Type:"Truck"},
    {Type_ID: "1",Type:"Truck"},
    {Type_ID: "1",Type:"Truck"},
    {Type_ID: "1",Type:"Truck"},


  ];
  constructor(public appSettings:AppSettings, public router: Router) {
    this.settings = this.appSettings.settings; 
    this.dataSource = new MatTableDataSource<Element>(this.data);
  }

  //==============================Filter table==================
  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  //============================Initialize paginator============
  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
  }


  //==================Change to edit form=====================
  changeform(){
    if(this.show == false){
      this.show =true;
      this.showAdd = false;
      this.showtable = false;
    }
  }

  //=================change to add form=======================
  AddVehicleType(){
    if(this.show == true){
         this.show = false;
    }

    this.showAdd = true;
    this.showtable = false;
  }


  //===============Vehicle POST==============================
  AddNewVehicleType(){
 
  }

  //================Get type to edit in object===============
  getTypeDetail(selected: number){
    this.changeform();
  }

  //===========================Show table ===================
  showTable(){
    this.showtable = true;
    this.showAdd = false;
    this.show = false;
  }
  ngOnInit(): void {
    if(localStorage.getItem("jwtToken") === null){
      this.router.navigateByUrl("login");
    }
  }

}
