import {  OnInit } from '@angular/core';
import { Settings } from 'src/app/app.settings.model';
import { AppSettings } from 'src/app/app.settings';
import { MatTableDataSource } from '@angular/material/table';
import { Component, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { Sections } from 'src/app/Models/Section';

export interface Element {
  Section_Type_Description: string;
  Section_ID: string;
  Section_Name: string;
  Section_Location: string;
  Section_Size: string;
  }

@Component({
  selector: 'app-section',
  templateUrl: './section.component.html',
  styleUrls: ['./section.component.scss']
})
export class SectionComponent implements OnInit {

  public displayedColumns = ['Section_ID', 'Section_Type_Description','Section_Location','Section_Size','Actions'];
  public dataSource: any;
  public settings: Settings;
  show: boolean = false;
  showAdd: boolean = false;
  hideTable: boolean = true;
  @ViewChild(MatPaginator) paginator: MatPaginator;

 data: Element[] = [
    {Section_ID: "1", Section_Name:"Oop grond", Section_Type_Description: "Field", Section_Location: "Noord van huis", Section_Size: "2 km"},
    {Section_ID: "2", Section_Name:"Bees Kraal", Section_Type_Description: "Livestock", Section_Location: "Langs Stoor", Section_Size: "5 km"},
    {Section_ID: "3", Section_Name:"Mielies 1", Section_Type_Description: "Land", Section_Location: "Wes van stoor", Section_Size: "1 km"},
  ];
  
 types: string[] = [
   "Field","Land","Livestock"
 ]

SectionEdit: Sections;
SectionAdd: Sections;
AdminPriv: string;
selectedValue: string;
SectionTypeSelect: string;
InfrastructureSelect: string;
  constructor(public appSettings:AppSettings) {
    this.settings = this.appSettings.settings; 
    this.dataSource = new MatTableDataSource<Element>(this.data);
  }
  ngOnInit(): void {
     this.AdminPriv = localStorage.getItem("PrivelageLevel");
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
  }

  changeform(){
    if(this.show == false){
      this.show =true;
      this.showAdd = false;
      this.hideTable = false;
    }
  }

  getSectionDetail(Id: number){
    //Implement function to pupulate edit field and populate vehicle edit\
    this.SectionEdit = new Sections;
    this.SectionEdit.Section_Type_Description = "New Type";
    this.changeform();
  }

  AddSection(){
    if(this.show == true){
         this.show = false;
    }
    this.SectionAdd = new Sections;
    this.showAdd = true;
    this.hideTable = false;
  }

  PutSection(){
    alert("Done");
  }

  deleteSection(){
    alert("Are you sure");
  }

  back(){
    this.show = false;
    this.hideTable = true;
    this.showAdd = false
  }

  SectionTypes(){
    
  }

}
