  addFarm(){
    if(this.show == true){
      this.show = false;
    } 
    this.FarmAdd = new PostFarm;
    this.showAdd = true;
    this.showtable = false;
    this.showF = false;
    this.showFAdd = false;
  }

  putFarm(){
    this.farmservice.putFarm(this.FarmEdit,this.currentEdit).subscribe(
      success=>{
        console.log(success);
      },
  
      error=>{
        console.log(error)
      }
    );
  }

  postFarm(){
    this.FarmAdd.Farm_ID = 7;
    this.FarmAdd.Is_Active = "1";
    
    this.ops = {   
      headers: new HttpHeaders({
        'Authorization': 'Bearer ' + localStorage.getItem("jwtToken")
      })
    };
    this.farmservice.postFarm(this.FarmAdd).subscribe( 
      (response) =>  {
          console.log(response);
      }
    );
  }

  deleteFarm(id:number){
    if(confirm("Are you sure to delete ?")) {
      this.farmservice.deleteFarm(id).subscribe(
        success=>{
          alert("Farm succesfully removed");
        },
        error=>{
          alert(error);
        }
      );
    }
  }

  showTable(){
    this.showtable = true;
    this.showAdd = false;
    this.show = false;
    this.showF = false;    
    this.showFAdd = false;  
  }
  
  Foreman(selected: number){
    if(selected == 1){
      this.showtable = false;
      this.showAdd = false;
      this.show = false;
      this.showF = true;
      this.showFAdd = false;
    }
    
    if(selected == 2){
      this.showtable = false;
      this.showAdd = false;
      this.show = false;
      this.showF = false;
      this.showFAdd =true;
    }

    if(selected == 3){
      this.showtable = false;
      this.showAdd = false;
      this.show = false;
      this.showF = true;
      this.showFAdd = false;
    }
  }

