import { Component, OnInit, ViewChild} from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { Settings } from '../app.settings.model';
import { AppSettings } from '../app.settings';
import { MatTableDataSource } from '@angular/material/table';
import {VehicleService, PostVehicleService, PostNotification} from '../Models/VehicleService';
import { FormGroup, Validators, FormBuilder, FormControl } from '@angular/forms';
import { SRProviders } from '../Models/SRProvider';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { vsService } from './vsService.service';
import { AngularFireUploadTask, AngularFireStorage } from '@angular/fire/storage';
import { Observable } from 'rxjs';

export interface Element{
  Value:number;
  Text: string;
}


@Component({
  selector: 'app-vehicle-service',
  templateUrl: './vehicle-service.component.html',
  styleUrls: ['./vehicle-service.component.scss']
})
export class VehicleServiceComponent implements OnInit {
  showtable: boolean = true;
  public displayedColumns = ['Vehicle_Service_ID','Vehicle_ID', 'Vehicle_Service_Milage', 'Vehicle_Service_Start_Date', 'Vehicle_Service_End_Date','Invoice','Actions'];
  public dataSource: any;
  public settings: Settings;
  show: boolean = false;
  showAdd: boolean = false;
  showService: boolean =false;
  form: FormGroup;

  invoiceSrc: string;
  basePath = '/invoices';                      
  downloadableURL = '';                      
  task: AngularFireUploadTask;

  submit1: boolean = false;
  submit2: boolean = false;

  @ViewChild(MatPaginator) paginator: MatPaginator;


  // providers:string[]=[
  //   'Toyota Service Shop','Nissan Service Shop','Mitsubishi Service Shop'
  // ];

  // types: string[] = [
  //   "Bakkie","Tractor","Car"
  // ]

  currentEdit: number;
  ops:any
  file:any;
  progressValue: Observable<number>;
  VehicleNotificationAdd: PostNotification = new PostNotification;
  VehicleServEdit: PostVehicleService = new PostVehicleService;
  VehicleServAdd: PostVehicleService = new PostVehicleService;
  active: Element[] = [{Value: 1, Text: "Yes"},{Value: 0, Text: "No"}];
  providers: SRProviders[]= [];
  data : VehicleService[] = [];
  AdminPriv: string;

  constructor(public appSettings:AppSettings, public router: Router, private formbuilder: FormBuilder, public dialog: MatDialog, private http : HttpClient, private vsService: vsService,private fireStorage: AngularFireStorage) { 
    this.settings = this.appSettings.settings; 
    this.form = this.formbuilder.group({  
      'Description':['', Validators.compose([Validators.required,Validators.maxLength(150)])],
      'Mileage':['', Validators.compose([Validators.required,Validators.maxLength(10)])],
      'Start':['', Validators.compose([Validators.required,Validators.maxLength(8)])],
      'End':['', Validators.compose([Validators.required,Validators.maxLength(8)])],
  
      Providers: new FormControl
    })
  }

  ngOnInit(): void {
   if(localStorage.getItem("jwtToken") === null){
      this.router.navigateByUrl("login");
   }else{
    this.AdminPriv = localStorage.getItem("PrivelageLevel");
    localStorage.setItem("currentFarm","7");
    this.getData();
    } 
  }

  data3:any;
  getData(){
    this.vsService.getVehicleService().subscribe( 
      (response) =>{
        this.data3 = response;
        console.log(this.data3);
        this.dataSource = new MatTableDataSource<VehicleService>(this.data3["vsList"]); 
        this.dataSource.paginator = this.paginator; 
      },
      error=>{
        alert("An error has occured, please login.");
        this.router.navigateByUrl("logout");
      }
    );

    this.vsService.getServiceProviders().subscribe( 
      (response) => {
            this.data3 = response;
            console.log(this.data3);
            this.providers = this.data3;
    }); 
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
  }

  changeform(){
    if(this.show == false){
      this.show =true;
      this.showAdd = false;
      this.showtable = false;
      this.showService =false;
    }
  }

  getVehicleServiceDetail(id:number){
    localStorage.setItem("CurrentEquipment", id.toString())
    this.ops = { 
      headers: new HttpHeaders({
        
        'Authorization': 'Bearer ' + localStorage.getItem("jwtToken")
      })
    };
    this.vsService.getVehicleServiceDetails(id).subscribe( 
      success => {
        this.data3 = success;
        this.VehicleServEdit = this.data3;
        this.currentEdit = id;
      }
    );
    this.changeform();
  }

  addVehicleService(){
    if(this.show == true){
      this.show = false;
    }
    this.VehicleServAdd = new PostVehicleService;
    this.showAdd = true;
    this.showtable = false;
    this.showService =false;
  }

  putVehicleService(){
    this.vsService.putVehicleService(this.VehicleServEdit, this.currentEdit).subscribe(
      success=>{
        console.log(success);
      },
  
      error=>{
        console.log(error)
      }
    );
  }

  postVehicleService(){
    this.ops = {    
      headers: new HttpHeaders({
        
        'Authorization': 'Bearer ' + localStorage.getItem("jwtToken")
      })
    };
    this.vsService.postVehicleService(this.VehicleServAdd).subscribe( 
      (response) =>  {
          console.log(response);
      }
    );
  }

  deleteVehicleService(id:number){
    if(confirm("Are you sure to delete ?")) {
      this.vsService.deleteVehicleService(id).subscribe(
        success=>{
          alert("Equipment succesfully removed");
        },
        error=>{
          alert(error);
        }
      );
    }
  }

  showTable(){
    this.showtable = true;
    this.showAdd = false;
    this.show = false;
    this.showService=false;
  }
  showServiceNotification(){
    if(this.show == true){
      this.show = false;
    }
    this.VehicleNotificationAdd = new PostVehicleService;
    this.showAdd=false;
    this.showtable=false;
    this.showService=true;
  }

  postServiceRequest(){
    
  }

  Download(){
    alert("Download started");
  }

  putServiceRequest(){
    alert("Done");
  }

  removeVehicleService(){
    
  }

  successfullyAddedService(){
    
  }

  editVehicleService(){
    
  }

  sentNotification(){

  }

  SubmitData = new VehicleService(); 
  PostData = new VehicleService();

  public async onSubmit() {

     
    if (this.file) {
      
      
       const filePath = `${this.basePath}/${this.SubmitData.VSD_ID}`;
       this.task =  this.fireStorage.upload(filePath, this.file);    
 
       // this.progress = this.snapTask.percentageChanges();
 
       this.progressValue = this.task.percentageChanges();
       (await this.task).ref.getDownloadURL().then(url => 
        {
          this.downloadableURL = url;
          this.postData()
        });  
     } else {  
       this.downloadableURL = ''; 
       this.postData();
      }      
   }

   postData(){
    
    this.PostData = this.SubmitData;
    this.PostData.VSD_Invoice = this.downloadableURL;
    this.http.post<VehicleService>("http://35.178.156.37/api/", this.PostData).subscribe(
      success=> {
        alert("Upload Successful!");

      }
    );
  }

  onSelectedFile(event){
    
    const reader = new FileReader();
    
    if(event.target.files && event.target.files.length) {
      const [file] = event.target.files;
      reader.readAsDataURL(file);
    
      reader.onload = () => {
        this.invoiceSrc = reader.result as string;
        this.form.patchValue({
          fileSource: reader.result
        });
      };
    }
       this.file = event.target.files[0];
        this.file.name = this.SubmitData.VSD_ID;
  }

}
