import {  OnInit } from '@angular/core';
import { Settings } from 'src/app/app.settings.model';
import { AppSettings } from 'src/app/app.settings';
import { MatTableDataSource } from '@angular/material/table';
import { Component, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { SectionTypes } from 'src/app/Models/SectionType';

export interface Element {
  Section_Type_ID: string;
  Section_Type_Description: string;
  }

@Component({
  selector: 'app-section-type',
  templateUrl: './section-type.component.html',
  styleUrls: ['./section-type.component.scss']
})
export class SectionTypeComponent implements OnInit {

  public displayedColumns = ['Section_Type_ID', 'Section_Type_Description',"Actions"];
  public dataSource: any;
  public settings: Settings;
  show: boolean = false;
  showAdd: boolean = false;
  @ViewChild(MatPaginator) paginator: MatPaginator;

 data: Element[] = [
    {Section_Type_ID: "1",Section_Type_Description: "Field"},
    {Section_Type_ID: "2",Section_Type_Description: "Livestock"},
    {Section_Type_ID: "3",Section_Type_Description: "Land"},
  ];
  
 types: string[] = [
   "Field","Livestock","Land"
 ]

SectionTypeEdit: SectionTypes;
SectionTypeAdd: SectionTypes;
AdminPriv: string;
selectedValue: string;
SectionSelect: string;
  constructor(public appSettings:AppSettings) {
    this.settings = this.appSettings.settings; 
    this.dataSource = new MatTableDataSource<Element>(this.data);
  }
  ngOnInit(): void {
     this.AdminPriv = localStorage.getItem("PrivelageLevel");
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
  }

  changeform(){
    if(this.show == false){
      this.show =true;
      this.showAdd = false;
    }
  }

  getSectionTypeDetail(Id: number){
    //Implement function to pupulate edit field and populate vehicle edit\
    this.SectionTypeEdit = new SectionTypes;
    this.SectionTypeEdit.Section_Type_Description = "New Type";
    this.changeform();
  }

  AddSectionType(){
    if(this.show == true){
         this.show = false;
    }
    this.SectionTypeAdd = new SectionTypes;
    this.showAdd = true;
  }

  PutSectionType(){
    alert("Done");
  }

  deleteSectionType(){
    alert("Are you sure");
  }

}
