import {  OnInit } from '@angular/core';
import { Settings } from 'src/app/app.settings.model';
import { AppSettings } from 'src/app/app.settings';
import { MatTableDataSource } from '@angular/material/table';
import { Component, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { Maintenance } from 'src/app/Models/Maintenance';

export interface Element {
    Maintenance_ID: string;
    Maintenance_Description: string;
    Maintenance_Start_Date: string;
    Maintenance_End_Date: string;
    Maintenance_Type_Description: string;
    Status_Description: string;
    Importance_Description: string;
  }

@Component({
  selector: 'app-maintenance-log',
  templateUrl: './maintenance-log.component.html',
  styleUrls: ['./maintenance-log.component.scss']
})
export class MaintenanceLogComponent implements OnInit {

  public displayedColumns = ['Maintenance_ID', 'Maintenance_Type_Description', 'Maintenance_Description', 'Maintenance_Start_Date','Maintenance_End_Date','Status_Description','Importance_Description','Actions'];
  public dataSource: any;
  public settings: Settings;
  show: boolean = false;
  showAdd: boolean = false;
  @ViewChild(MatPaginator) paginator: MatPaginator;

 data: Element[] = [
    {Maintenance_ID: "1",Maintenance_Type_Description: "Infrastructure", Maintenance_Description: 'Check Barn wiring', Maintenance_Start_Date: "05/07/2020" , Maintenance_End_Date: "05/07/2020", Status_Description: 'In Progress', Importance_Description: 'Medium'},
    {Maintenance_ID: "2",Maintenance_Type_Description: "Equipment", Maintenance_Description: 'Clean Ploughs', Maintenance_Start_Date: "02/01/2020" , Maintenance_End_Date: "05/01/2020", Status_Description: 'Completed', Importance_Description: 'High'},
    {Maintenance_ID: "3",Maintenance_Type_Description: "Other", Maintenance_Description: 'Sweep Barn', Maintenance_Start_Date: "22/05/2020" , Maintenance_End_Date: "22/05/2020", Status_Description: 'Incomplete', Importance_Description: 'Low'},
  ];
  
 types: string[] = [
  "Wiring","Structural","Security"
 ]

 infrastructures: string[] = [
   "Building1","Building2"
 ]

 staff: string[] = [
   "John", "Petrus"
 ]

 equipment: string[] =[
   "Trailer", "Plow"
 ]

 status: string[] =[
  "In Progress", "Completed", "Pending"
]

importance: string[] =[
  "High", "Low", "Medium"
]

MaintenanceEdit: Maintenance;
MaintenanceAdd: Maintenance;
AdminPriv: string;
selectedValue: string;
EquipmentSelect: string;
StatusSelect: String;
ImportanceSelect: string;
InfrastructureSelect: string;
StaffSelect: string;
showtable = true;
  constructor(public appSettings:AppSettings) {
    this.settings = this.appSettings.settings; 
    this.dataSource = new MatTableDataSource<Element>(this.data);
  }
  ngOnInit(): void {
     this.AdminPriv = localStorage.getItem("PrivelageLevel");
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
  }

  changeform(){
    if(this.show == false){
      this.show =true;
      this.showAdd = false;
      this.showtable = false;
    }
  }

  getMaintenanceDetail(Id: number){
    //Implement function to pupulate edit field and populate vehicle edit\
    this.MaintenanceEdit = new Maintenance;
    this.MaintenanceEdit.Maintenance_Description = "Clean Ploughs";
    this.MaintenanceEdit.Maintenance_Type_Description = "Equipment";
    this.MaintenanceEdit.Infrastructure_Description= "Barn";
    this.MaintenanceEdit.Equipment_Description= "Plough";
    this.MaintenanceEdit.Status_Description= "Active";
    this.MaintenanceEdit.Importance_Description= "High";
    this.MaintenanceEdit.Staff_Name= "John";
    this.MaintenanceEdit.Maintenance_Start_Date= "22/07/2020";
    this.MaintenanceEdit.Maintenance_End_Date= "25/07/2020";
  
    this.selectedValue = this.MaintenanceEdit.Maintenance_Type_Description;
    this.EquipmentSelect = this.MaintenanceEdit.Equipment_Description;
    this.InfrastructureSelect = this.MaintenanceEdit.Infrastructure_Description;
    this.StatusSelect = this.MaintenanceEdit.Status_Description;
    this.ImportanceSelect = this.MaintenanceEdit.Importance_Description;
    this.StaffSelect = this.MaintenanceEdit.Staff_Name;


    this.changeform();
  }

  AddMaintenance(){
    if(this.show == true){
         this.show = false;
    }
    this.MaintenanceAdd = new Maintenance;
    this.showAdd = true;
    this.showtable = false;
  }

  PutMaintenance(){
    alert("Done");
  }

  deleteMaintenance(){
    alert("Are you sure");
  }

  back(){
    this.showtable = true;
    this.show = false;
    this.showAdd = false;
  }

}
